import DataGroup from "jx/editor/editionitems/DataGroup";
export default class QuizThemeItem extends DataGroup {
    constructor(dataObject) {
        super(dataObject);
        this.data.exportType = "DataGroup";
        this.data.children = this.data.children || [
            { id: "title", type: "Text", value: this.data.title || "" },
            { id: "picto", type: "Image", asset: { preload: false, src: "images/themes/" + this.data.id + ".png" } },
            { id: "titleColor", type: "Color", value: "#000000" },
            { id: "titleSize", type: "Number", min: 5, value: 12 }
        ];
    }
    firstInit() {
        if (!this.titleField) {
            this.titleField = this.findChildById("title");
            console.log(this.titleField);
            this.titleField.onchanged.add(evt => {
                // if (this.titleField.value != this.data.title) {
                //     this.setTitle(this.titleField.value);
                //     this.infosView.onchanged.dispatch({target: this.infosView});
                let editionPage = this.controller.mainController;
                let gabaritView = editionPage.gabaritView;
                editionPage.refreshEditionView();
                editionPage.showTab(this.id);
                // }
            });
            // this.nameField.value = this.data.title;
            // this.nameField.onchanged.dispatch({target: this.nameField});
            // this.onchanged.dispatch({target: this});
        }
    }
}
